@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
}

nav {
  background: rgb(185, 1, 1);
  width: 100vw;
  padding: 1.2rem 4rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  position: fixed;
  top: 0;
  z-index: 99;
}
.nav-links a,
.cart a {
  text-decoration: none;
  margin-right: 2rem;
  color: rgb(245, 245, 245);
  font-size: 14px;
  display: inline-block;
}
.nav-links a:hover,
.cart a:hover {
  text-decoration: underline;
  color: rgb(220, 220, 220);
}
.center-items-v {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}
.nav-links svg,
.cart svg {
  margin-right: 5px;
}
.social-links a {
  color: rgb(245, 245, 245);
  text-decoration: none;
  font-size: 13px;
  margin-left: 2rem;
}
.social-links a:hover {
  color: rgb(220, 220, 220);
}
.cart-quantity {
  font-size: 12px;
  background: rgb(255, 247, 0);
  color: rgb(9, 9, 9);
  height: 1.2rem;
  width: 1.2rem;
  border-radius: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  border: none;
  outline: none;
}

.hero-section{
    min-height: 90vh;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background: green;
    color: #fff
}
.hero-section h2{
    font-size: 40px;
}
.products-container {
  width: 90vw;
  margin: auto;
  padding: 5rem 0;
}
.products-grid {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(9rem, 17rem));
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  grid-gap: 1.2rem;
  gap: 1.2rem;
}
.product {
  padding: 0.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 12px;
}
.product img {
  height: 200px;
  width: auto;
  border-radius: 9px;
}
.product-desc {
  margin: 0.5rem 0 0 0;
  font-size: 14px;
  opacity: 0.7;
}
.product-name {
  margin: 0.2rem 0;
  font-size: 16px;
  font-weight: 300;
}
.product-price {
  font-size: 22px;
  margin: 0.5rem;
  font-weight: 600;
}
.product-price sup {
  text-transform: uppercase;
  font-size: 12px;
  font-weight: 400;
  margin-right: 0.2rem;
}
.product-cta button{
    padding: 6px 12px;
    border: none;
    outline: none;
    color: #fff;
    background: rgb(13, 149, 253);
    font-weight: 700;
    font-size: 20px;
}
.product-cta .minus{
  border-radius: 7px 0 0 7px;
}
.product-cta .add{
  border-radius: 0 7px 7px 0;
}
.product-cta button:hover{
    cursor: pointer;
    background: rgb(60, 171, 255);
}
.product-cta .cart-cta{
 padding: 6px 18px;
}
.cart-cta .count{
    font-size: 14px;
    font-weight: 300;
    vertical-align: middle;
}

footer{
    background: rgb(17, 17, 17);
    color: #fafafa;
    padding: 3rem 3rem 0.5rem 3rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    min-height: 20vh;
}
.footer-details{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}
.copyright{
    text-align: center;
    font-size: 14px;
    color: rgb(175, 175, 175);
}
.cart-container {
  margin: 5rem auto;
  width: 90vw;
  min-height: 60vh;
}
.cart-content {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

/* Items */
.cart-items {
  -webkit-flex: 2 1;
          flex: 2 1;
  padding: 0.7rem;
  margin-right: 0.7rem;
  font-size: 15px;
  text-transform: uppercase;
  font-weight: 300;
}
.cart-item {
  display: grid;
  grid-template-columns: 1fr 2fr 1fr 1fr 1fr;
  -webkit-align-items: center;
          align-items: center;
  padding-top: 0.7rem;
  margin-top: 0.7rem;
  border-top: 1px solid rgb(216, 216, 216);
}
.cart-item img {
  height: 70px;
  width: auto;
}
.item-price {
  font-weight: 600;
}
.item-price span {
  font-weight: 300;
}
.change-amount {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.decrease-icon,
.increase-icon,
.remove-icon {
  color: gray;
  cursor: pointer;
  margin: 0 0.2rem;
}
.remove-icon {
  color: rgb(225, 3, 3);
}
.decrease-icon:hover,
.increase-icon:hover {
  color: rgb(60, 171, 255);
}

.remove-icon:hover {
  color: rgb(255, 74, 74);
}

/* Totals */
.cart-totals {
  -webkit-flex: 1 1;
          flex: 1 1;
  padding: 0.7rem;
  margin-top: 0.7rem;
  border-radius: 12px;
  box-shadow: 0px 9px 21px -6px rgba(171, 171, 171, 0.75);
  -webkit-box-shadow: 0px 9px 21px -6px rgba(171, 171, 171, 0.75);
  -moz-box-shadow: 0px 9px 21px -6px rgba(171, 171, 171, 0.75);
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.cart-totals h3 {
  font-size: 22px;
  font-weight: 500;
  margin: 0.9rem 0;
}
.cart-totals button {
  display: inline-block;
  padding: 15px 22px;
  margin-bottom: 0.9rem;
  border: none;
  outline: none;
  border-radius: 7px;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: 500;
  color: white;
  background: rgb(13, 149, 253);
  width: 70%;
  cursor: pointer;
}
.cart-totals button:hover {
  background: rgb(60, 171, 255);
}
.cart-totals .clear-cart {
  background: gray;
}
.cart-totals .clear-cart:hover {
  background: rgb(255, 74, 74);
}
.cart-totals a {
  text-decoration: none;
  color: gray;
  margin-bottom: 0.9rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: flex-start;
          align-items: flex-start;
}
.cart-totals a span {
  margin-right: 0.2rem;
}

